/**
 * PREMIUM GATING MIDDLEWARE
 *
 * Ensures users have appropriate subscription tier before accessing premium features.
 *
 * Subscription Tiers:
 * - FREE: Basic invoicing, email reminders (2 per month demo)
 * - PAID: All premium features (SMS, Letters, AI Calls, Agency Handoff)
 *
 * Premium Features:
 * - Twilio SMS reminders
 * - Physical letters via Lob
 * - AI voice collection calls
 * - Agency escalation
 * - Advanced analytics
 * - Priority support
 */

import { db } from '@/lib/firebase';
import { User } from '@/types/models';
import { logWarn } from '@/utils/logger';

/**
 * Premium feature types
 */
export type PremiumFeature =
  | 'sms_reminders'
  | 'physical_letters'
  | 'ai_voice_calls'
  | 'agency_handoff'
  | 'advanced_analytics'
  | 'priority_support'
  | 'unlimited_collections';

/**
 * Feature to tier mapping
 */
const FEATURE_TIER_REQUIREMENTS: Record<PremiumFeature, 'free' | 'paid'> = {
  sms_reminders: 'paid',
  physical_letters: 'paid',
  ai_voice_calls: 'paid',
  agency_handoff: 'paid',
  advanced_analytics: 'paid',
  priority_support: 'paid',
  unlimited_collections: 'paid',
};

/**
 * Feature cost estimates (for display to users)
 */
const FEATURE_COSTS: Record<PremiumFeature, { perUse: number; description: string }> = {
  sms_reminders: {
    perUse: 0.04,
    description: 'SMS reminder sent to client',
  },
  physical_letters: {
    perUse: 1.20,
    description: 'Physical letter sent by post',
  },
  ai_voice_calls: {
    perUse: 1.50,
    description: '5-minute AI collection call',
  },
  agency_handoff: {
    perUse: 0, // Commission-based
    description: 'Escalation to collection agency',
  },
  advanced_analytics: {
    perUse: 0,
    description: 'Access to advanced analytics dashboard',
  },
  priority_support: {
    perUse: 0,
    description: '24/7 priority email support',
  },
  unlimited_collections: {
    perUse: 0,
    description: 'Unlimited automated collection reminders',
  },
};

/**
 * Check if user has access to premium feature
 */
export async function checkPremiumAccess(
  userId: string,
  feature: PremiumFeature
): Promise<{
  hasAccess: boolean;
  reason?: string;
  upgradeRequired?: boolean;
}> {
  try {
    // 1. Get user
    const userDoc = await db.collection('users').doc(userId).get();

    if (!userDoc.exists) {
      return {
        hasAccess: false,
        reason: 'User not found',
      };
    }

    const user = userDoc.data() as User;

    // 2. Check subscription tier
    const requiredTier = FEATURE_TIER_REQUIREMENTS[feature];

    if (requiredTier === 'paid' && user.subscriptionTier === 'free') {
      return {
        hasAccess: false,
        reason: 'This feature requires a paid subscription',
        upgradeRequired: true,
      };
    }

    // 3. Check if collections are enabled
    if (!user.collectionsEnabled) {
      return {
        hasAccess: false,
        reason: 'Collections features are disabled for your account',
      };
    }

    // 4. Special handling for free tier demo
    if (user.subscriptionTier === 'free' && feature === 'unlimited_collections') {
      // Check demo usage
      const currentMonth = new Date().getMonth();
      const lastResetMonth = user.lastDemoResetDate?.toDate().getMonth();

      // Reset counter if new month
      if (lastResetMonth !== currentMonth) {
        await db.collection('users').doc(userId).update({
          collectionsDemoUsedThisMonth: 0,
          lastDemoResetDate: new Date(),
        });
      }

      // Check if demo limit reached
      if (user.collectionsDemoUsedThisMonth >= 2) {
        return {
          hasAccess: false,
          reason: 'Free tier demo limit reached (2 per month)',
          upgradeRequired: true,
        };
      }
    }

    return {
      hasAccess: true,
    };

  } catch (error) {
    logWarn('Premium access check failed', { userId, feature, error });
    return {
      hasAccess: false,
      reason: 'Unable to verify subscription status',
    };
  }
}

/**
 * Validate premium access or throw error
 * Use this in API routes to enforce premium gating
 */
export async function requirePremiumAccess(
  userId: string,
  feature: PremiumFeature
): Promise<void> {
  const result = await checkPremiumAccess(userId, feature);

  if (!result.hasAccess) {
    const error: any = new Error(result.reason || 'Access denied');
    error.statusCode = 402; // Payment Required
    error.upgradeRequired = result.upgradeRequired;
    throw error;
  }
}

/**
 * Increment demo usage counter for free tier
 */
export async function incrementDemoUsage(userId: string): Promise<void> {
  try {
    const userDoc = await db.collection('users').doc(userId).get();
    const user = userDoc.data() as User;

    if (user.subscriptionTier === 'free') {
      const currentMonth = new Date().getMonth();
      const lastResetMonth = user.lastDemoResetDate?.toDate().getMonth();

      // Reset if new month
      if (lastResetMonth !== currentMonth) {
        await db.collection('users').doc(userId).update({
          collectionsDemoUsedThisMonth: 1,
          lastDemoResetDate: new Date(),
        });
      } else {
        await db.collection('users').doc(userId).update({
          collectionsDemoUsedThisMonth: (user.collectionsDemoUsedThisMonth || 0) + 1,
        });
      }
    }
  } catch (error) {
    logWarn('Failed to increment demo usage', { userId, error });
  }
}

/**
 * Get feature details for UI display
 */
export function getFeatureInfo(feature: PremiumFeature): {
  name: string;
  description: string;
  requiredTier: 'free' | 'paid';
  costPerUse: number;
  costDescription: string;
} {
  const names: Record<PremiumFeature, string> = {
    sms_reminders: 'SMS Reminders',
    physical_letters: 'Physical Letters',
    ai_voice_calls: 'AI Voice Calls',
    agency_handoff: 'Agency Escalation',
    advanced_analytics: 'Advanced Analytics',
    priority_support: 'Priority Support',
    unlimited_collections: 'Unlimited Collections',
  };

  const descriptions: Record<PremiumFeature, string> = {
    sms_reminders: 'Send automated SMS text reminders to clients for overdue invoices',
    physical_letters: 'Send professional collection letters via postal mail',
    ai_voice_calls: 'AI-powered phone calls to collect overdue payments',
    agency_handoff: 'Escalate difficult cases to professional collection agencies',
    advanced_analytics: 'Detailed insights into payment patterns and client behavior',
    priority_support: '24/7 priority email support with faster response times',
    unlimited_collections: 'Unlimited automated email collection reminders',
  };

  const cost = FEATURE_COSTS[feature];

  return {
    name: names[feature],
    description: descriptions[feature],
    requiredTier: FEATURE_TIER_REQUIREMENTS[feature],
    costPerUse: cost.perUse,
    costDescription: cost.description,
  };
}

/**
 * Get all premium features with access status for user
 */
export async function getUserFeatureAccess(userId: string): Promise<
  Array<{
    feature: PremiumFeature;
    name: string;
    hasAccess: boolean;
    requiredTier: 'free' | 'paid';
    costPerUse: number;
  }>
> {
  const features: PremiumFeature[] = [
    'sms_reminders',
    'physical_letters',
    'ai_voice_calls',
    'agency_handoff',
    'advanced_analytics',
    'priority_support',
    'unlimited_collections',
  ];

  const results = await Promise.all(
    features.map(async (feature) => {
      const access = await checkPremiumAccess(userId, feature);
      const info = getFeatureInfo(feature);

      return {
        feature,
        name: info.name,
        hasAccess: access.hasAccess,
        requiredTier: info.requiredTier,
        costPerUse: info.costPerUse,
      };
    })
  );

  return results;
}

/**
 * Get upgrade CTA message for feature
 */
export function getUpgradeMessage(feature: PremiumFeature): {
  title: string;
  message: string;
  ctaText: string;
  ctaUrl: string;
} {
  const info = getFeatureInfo(feature);

  return {
    title: `Upgrade to use ${info.name}`,
    message: `${info.description}. This feature is available on our paid plan.`,
    ctaText: 'Upgrade to Paid Plan',
    ctaUrl: '/settings/billing/upgrade',
  };
}

/**
 * Check if user can perform action based on subscription
 * Returns true if allowed, false if blocked
 */
export async function canPerformAction(params: {
  userId: string;
  action: 'send_sms' | 'send_letter' | 'make_ai_call' | 'escalate_agency';
}): Promise<{
  allowed: boolean;
  reason?: string;
  requiresUpgrade?: boolean;
}> {
  const featureMap = {
    send_sms: 'sms_reminders' as PremiumFeature,
    send_letter: 'physical_letters' as PremiumFeature,
    make_ai_call: 'ai_voice_calls' as PremiumFeature,
    escalate_agency: 'agency_handoff' as PremiumFeature,
  };

  const feature = featureMap[params.action];
  const access = await checkPremiumAccess(params.userId, feature);

  return {
    allowed: access.hasAccess,
    reason: access.reason,
    requiresUpgrade: access.upgradeRequired,
  };
}

/**
 * Log premium feature usage for billing/analytics
 */
export async function logPremiumFeatureUsage(params: {
  userId: string;
  feature: PremiumFeature;
  invoiceId?: string;
  cost?: number;
}): Promise<void> {
  try {
    await db.collection('premium_feature_usage').add({
      userId: params.userId,
      feature: params.feature,
      invoiceId: params.invoiceId,
      cost: params.cost || FEATURE_COSTS[params.feature].perUse,
      timestamp: new Date(),
    });
  } catch (error) {
    logWarn('Failed to log premium feature usage', params);
  }
}
